/* eslint-disable @typescript-eslint/no-explicit-any */
"use client";
import * as React from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";

export default function Page() {
  const [q, setQ] = React.useState("");
  const [rows, setRows] = React.useState<any[]>([]);
  const [err, setErr] = React.useState<string | undefined>();
  const [loading, setLoading] = React.useState(false);
  async function run() {
    if (!q) return;
    setLoading(true);
    setErr(undefined);
    try {
      const r = await fetch(`/api/search?q=${encodeURIComponent(q)}`);
      const j = await r.json();
      if (!r.ok) throw new Error(j?.error || "Search failed");
      setRows(j?.data?.array ?? j?.results ?? []);
    } catch (e: any) {
      setErr(e.message);
    } finally {
      setLoading(false);
    }
  }
  return (
    <div className="px-4 lg:px-6 space-y-4">
      <Card className="rounded-2xl">
        <CardHeader>
          <CardTitle>Zoek op artikel</CardTitle>
        </CardHeader>
        <CardContent className="flex gap-2">
          <Input
            value={q}
            onChange={(e) => setQ(e.target.value)}
            placeholder="Artikelnaam / nummer"
            onKeyDown={(e) => e.key === "Enter" && run()}
          />
          <Button onClick={run} disabled={!q || loading}>
            {loading ? "Zoekenâ€¦" : "Zoek"}
          </Button>
          {err && <span className="text-sm text-rose-600">{err}</span>}
        </CardContent>
      </Card>
      <Card className="rounded-2xl">
        <CardHeader>
          <CardTitle>Resultaten</CardTitle>
        </CardHeader>
        <CardContent className="px-0">
          <div className="overflow-x-auto">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>ID</TableHead>
                  <TableHead>Naam</TableHead>
                  <TableHead>Merk</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {rows.length === 0 && (
                  <TableRow>
                    <TableCell colSpan={3} className="text-muted-foreground">
                      Geen resultaten
                    </TableCell>
                  </TableRow>
                )}
                {rows.map((r: any) => (
                  <TableRow key={r?.articleId ?? r?.id}>
                    <TableCell className="font-medium">
                      {r?.articleId ?? r?.id}
                    </TableCell>
                    <TableCell>{r?.articleName ?? r?.name}</TableCell>
                    <TableCell>{r?.supplierName ?? r?.brand}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
